.universal-viewer.viewer {

  margin-bottom: rv(2) + rv(-4) +rv(2);

  &::before {
    content: "";
    height: rv(2);
    width: 100%;
    background: $black;
    display: block;
    margin-bottom: rv(-4); } }

.uv {
  background-color: $black !important;
  border-bottom-right-radius: $borderRadius;

  .headerPanel {
    color: $white !important;

    .options {
      background-color: neutral(95) !important; } }


  .overlays {
    background: rgba($black,0.5) !important; }

  .leftPanel, .rightPanel {
    background: neutral(95) !important; }

  .rightPanel {
    main {
      color: $white !important; } } }


